.movies {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}
.movies a{
    text-decoration: none;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width:45%;
}
.movies .movie {
    width: 100%;
    padding: 20px;
    border-radius: 5px ;  
    color: #b2b3bc;
    background-color: white;
    margin-bottom: 70px;
    display:flex;
    position: relative;
    font-weight: 300;
    word-wrap: break-word;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie img {
    position: relative;
    top: -50px;
    margin-right: 30px;
    max-width: 150px;
    width: 100%;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title, .movie .movie__year {
    font-weight: 300;
    margin: 0;
}
.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}
.movie .movie__genres{
    list-style-type: none;   
    padding: 0;
    margin: 5px 0px; 
    display: flex;
    flex-wrap: wrap;
}
.movie__genres li{
    margin-right: 10px;
    font-size: 14px;
}