.detail{
    width : 80%;
    margin-top: 30px;
    display:flex;
    background-color: white;
    padding: 20px;
    height: fit-content;
}
.detail img{
    max-height: 500px;
}
.detail__title{
    font-size: 35px;
}
.detail__year{
    font-size: 20px;
}
.detail__data{
    margin-left: 20px;
}
.detail__genres{
    list-style-type: none;
    display: flex;
    margin-bottom: 10px;
}
.detail__genres__genre{
    padding-right:10px;
}